* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  margin: 0px;
}

.contenedor-logo-y-nombre__logo-pagina {
  width: 45px;
}

.contenedor-logo-y-nombre__nombre-pagina {
  color: white;
  display: inline-block;
}

.cabecera {
  background-color: rgb(27, 37, 128);
  width: 100%;
  position: sticky;
  padding: 10px;
}
.cabecera .cabecera__menu-navegacion-nav li {
  list-style-type: none;
  font-family: "Staatliches";
}
.cabecera .cabecera__menu-navegacion-nav a {
  text-decoration: none;
  color: white;
}
.cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera {
  font-size: 14px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
  width: 79%;
}
.cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa .menu-hamburguesa__enlaceRegistro {
  width: 50px;
  border-radius: 0.5em;
  padding: 6px;
  color: rgb(27, 37, 128);
  background-color: rgb(204, 166, 31);
}
.cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__nav-label, .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__nav-input {
  display: none;
}
.cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .nav-label__icono-hamburguesa {
  width: 30px;
}
.cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__contenedor-logo-y-nombre {
  color: white;
  font-family: "Staatliches";
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3px;
}

@media screen and (min-width: 378px) and (max-width: 520px) {
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__nav-label {
    display: block;
    cursor: pointer;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-flow: column wrap;
    background-color: rgb(204, 166, 31);
    margin-left: 250px;
    margin-right: 20px;
    margin-top: 210px;
    width: 160px;
    height: 250px;
    position: absolute;
    clip-path: circle(0 at center);
    transition: clip-path 1s ease-in-out;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa li a {
    color: black;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa .menu-hamburguesa__enlaceRegistro {
    color: rgb(0, 0, 0);
    background-color: rgb(204, 166, 31);
    border-radius: 0;
    padding: 0;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .nav-label__icono-hamburguesa {
    display: flex;
    top: 0;
    right: 0;
    padding: 5px;
    cursor: pointer;
    background-color: rgb(204, 166, 31);
    position: relative;
    z-index: 1;
  }
  .cabecera .cabecera__menu-navegacion-nav .lista-cabecera__nav-input:checked + .lista-cabecera__menu-hamburguesa {
    clip-path: circle(100% at center);
  }
}
@media screen and (max-width: 378px) {
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera {
    position: relative;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__nav-label {
    display: block;
    cursor: pointer;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa {
    flex-flow: column wrap;
    background-color: rgb(204, 166, 31);
    margin-left: 180px;
    margin-top: 220px;
    width: 130px;
    height: 250px;
    padding-top: 35px;
    padding-bottom: 35px;
    position: fixed;
    clip-path: circle(0 at center);
    transition: clip-path 1s ease-in-out;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa li a {
    color: black;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa .menu-hamburguesa__enlaceRegistro {
    color: rgb(0, 0, 0);
    background-color: rgb(204, 166, 31);
    border-radius: 0;
    padding: 0;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .nav-label__icono-hamburguesa {
    display: flex;
    top: 0;
    right: 0;
    padding: 5px;
    cursor: pointer;
    background-color: rgb(204, 166, 31);
    position: absolute;
    z-index: 1;
  }
  .cabecera .cabecera__menu-navegacion-nav .lista-cabecera__nav-input:checked + .lista-cabecera__menu-hamburguesa {
    clip-path: circle(100% at center);
  }
}
.pie-pagina {
  background-color: rgb(27, 37, 128);
  position: sticky;
  width: 100%;
  height: 55px;
  padding: 10px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
}
.pie-pagina .pie-pagina__contenedor-redes-sociales {
  justify-self: flex-start;
}
.pie-pagina .pie-pagina__contenedor-redes-sociales a {
  color: #ffffff;
  font-family: "Staatliches";
  font-size: 12px;
}
.pie-pagina .pie-pagina__contenedor-redes-sociales .contenedor-redes-sociales__icono-insta {
  width: 18px;
}
.pie-pagina .pie-pagina__contenedor-redes-sociales .contenedor-redes-sociales__icono_facebook {
  width: 18px;
}
.pie-pagina .pie-pagina__contenedor-redes-sociales .contenedor-redes-sociales__enlace-insta {
  justify-self: flex-start;
}
.pie-pagina .pie-pagina__contenedor-redes-sociales .contenedor-redes-sociales__enlace-facebook {
  justify-self: center;
}
.pie-pagina .pie-pagina__enlace-invierte {
  justify-self: flex-end;
  color: #ffffff;
  font-family: "Staatliches";
  font-size: 12px;
}
.pie-pagina .pie-pagina__enlace-pag-404 {
  color: #ffffff;
  font-family: "Staatliches";
  font-size: 12px;
}
.pie-pagina .pie_pagina__enlace-pag-perfil-usuario {
  color: #ffffff;
  font-family: "Staatliches";
  font-size: 12px;
}

.main-pagina-producto {
  background-color: rgb(105, 120, 255);
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main-pagina-producto .section__contenedor-producto {
  background-color: rgb(27, 37, 128);
  width: 574px;
  padding: 20px;
  margin: 20px;
}
.main-pagina-producto .section__contenedor-producto .contenedor-producto__logo-bitcoin-y-titulo {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 12px;
}
.main-pagina-producto .section__contenedor-producto .contenedor-producto__logo-bitcoin-y-titulo h2 {
  font-size: 30px;
  font-family: "Staatliches";
  color: white;
  padding-top: 36px;
}
.main-pagina-producto .section__contenedor-producto .contenedor-producto__logo-bitcoin-y-titulo img {
  width: 40px;
  align-self: flex-end;
}
.main-pagina-producto .section__contenedor-producto #contenedor-producto__crypto-widget-coin-list {
  width: 54%;
  margin: 0 auto;
  margin-top: 20px;
}
.main-pagina-producto .section__contenedor-producto p {
  font-size: 16px;
  color: white;
  font-family: "Inter", sans-serif;
  display: inline;
}
.main-pagina-producto .section__contenedor-producto h3 {
  color: white;
  font-size: 16px;
  font-family: "Staatliches";
  display: inline;
}
.main-pagina-producto .section__contenedor-producto .contenedor-producto__info-crypto-bitcoin {
  padding-top: 30px;
