  justify-content: space-between;
  align-items: center;
}
.main-pagina-producto .section__contenedor-producto .cuarto-dato-crypto__enlace-evolucion-precio {
  padding: 5px;
  width: 96px;
  text-decoration: underline;
  background-color: rgb(204, 166, 31);
  font-family: "Staatliches";
  border-radius: 0.6em;
  color: black;
}

